import { Controller, Commands, Block } from '@ali/4ever-cangjie';
import { ColorBlocks } from '@ali/4ever-mo';

export function deleteCallout(controller: Controller, key: string) {
  const node = controller.value.document.getNode(key);
  if (!ColorBlocks.isColorBlocks(node)) {
    return controller;
  }
  controller.command(Commands.removeNodeByKey, key);
  return controller;
}
